  @import url('https://fonts.googleapis.com/css2?family=Jost:wght@300&display=swap');


*{
padding: 0;
margin: 0;
font-family: "Jost",sans-serif;
}
body{
    background: #f3f2ef;
    display :flex;
    justify-content:center;
    align-items:center;
    flex-wrap: wrap;
    height: 100vh;
    width: 100vw;
    font-family: "Jost",sans-serif;
}
html{
    font-size: 100%;
    height: 100%;
}
html{
    background: #eee;
}
.box{
    width: 25vw;
    height: 60vh;
    border-radius: 0.5rem;
    box-shadow: 0 0.2rem 3rem rgba(0,0,0,0.2) ;
    background: #f28715;
    position: relative;
    overflow: hidden;
    transform: translate3d(0,0,0);
    min-width: 20rem;
    min-height: 35rem;

}
.wave-one{
    opacity:0.3;
    position: absolute;
    top: 120%;
    left: 50%;
    background: white;
    width: 50rem;
    height: 52rem;
    margin-left: -25rem;
    margin-top: -20rem;
    transform-origin: 50% 48%;
    border-radius: 43%;
    animation: drift 3000ms infinite linear;
    z-index: 1;
  
}

.wave-three{
    background: #fcecc9;
    position: absolute;
    top: 120%;
    left: 50%;
    width: 50rem;
    height: 52rem;
    margin-left: -21rem;
    margin-top: -16rem;
    transform-origin: 50% 48%;
    border-radius: 49%;
    animation: drift 6000ms infinite linear;
    z-index: 2 !important;
    opacity: 0.2;
}
.wave-two{
    background: #fef4de;
    position: absolute;
    top: 120%;
    left: 50%;
    width: 50rem;
    height: 52rem;
    margin-left: -21rem;
    margin-top: -13rem;
    transform-origin: 50% 48%;
    border-radius: 43%;
    animation: drift 5500ms infinite linear;
    z-index: 3 !important;
    opacity: 0.5;
}

.box:after{
    content: "";
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index:11;
    transform: translate3d(0,0,0);
}

@keyframes drift{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}
.info{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 60vh;
    z-index: 4;
}
.location{
    margin-top: 5.5rem;
    text-align: center;
    font-weight: 800;
    font-size: 3.5rem;
    text-transform: capitalize;
}
.fa-street-view{
    animation: rotates 500ms linear infinite alternate;
}
@keyframes rotates {
    from{
        transform: translateX(-0.5rem);
    }
    to{
        transform: translateX(0.5rem);
    }
}
#date{
    text-align: center;
    margin-top: 0.5rem;
    color: #57606f;
    font-size: 1.2rem;
    font-weight: 300;
    text-transform: uppercase;
}
.temp{
    margin-top:2.5rem;
    text-align: center;
    font-size: 2rem;
    
}
.tempmin_max{
    text-align: center;
    margin-top: 0.3rem;
    font-weight: 800;
    font-size: 1rem;
    color: #000000;
}

#weathercon{
    height: 55%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3em;
}
#weathercon .fas{
font-size: 6rem;
animation: fas 3s linear infinite alternate;
}
@keyframes fas-anime {
    from{
        transform: scale(1.1);

    }
    to{
        transform: scale(1.5);

    }
}
@media(max-width:600px){
    .box{
        width: 90vw;
        height: 80vh;
    }
}
/* .wave{
    top: 85%;
} */
#weathercon{
    font-size: 5rem;
}
.info{
    font-size: 1.5rem;
    
}
@media(max-height: 500px){
    .box{
        height: 80vh;
    }
    .wave{
        top:115%
    }
}
body>span{
    width: 100vw;
    text-align: center;
    color: grey;
}
.inputData{
    width:100%;
    height: 13vh;
    display: flex;
    justify-content: center;
    align-items: center;

}
.inputFeild{
    width: 50%;
    margin: auto;
    border-radius: 5rem;
    padding: 1rem 1rem;
    outline: none;
    font-size: 1.5rem;
}
.errorMsg{
    text-align: center;
    font-size: 2rem;
    margin: 2rem auto;
}